<template>
    <div class="orderList" :class="{themeL:bindThemeL}">
        <div class="orderItem" v-for="(orderItem,orderIndex) in lists" :key="'orderItem'+orderIndex">
            <div class="orderHead">
                <div class="orderTitle" v-show="orderItem.isMultiShop==1&&orderItem.billCate2!=6"
                     @click="shopOrderDetailMulti(orderItem)">多店铺订单
                </div>
                <div class="orderTitle" v-show="orderItem.isMultiShop==1&&orderItem.billCate2==6"
                     @click="shopOrderDetailMulti(orderItem)">已取消订单
                </div>
                <div class="orderHeadInfo"
                     v-show="orderItem.isMultiShop==0&&orderItem.billCate2!=2||orderItem.billCate2==2">
                    <div class="orderHeadLeft" @click="goToShopPage(orderItem.shop.shopCode)">
                        <div>{{orderItem.shop.shopName}}</div>
                        <img src="@/assets/images/mall/mall_icon_more.png" alt="">
                    </div>
                    <div class="orderHeadRight" @click="callShop(orderItem)">
                        <img src="@/assets/images/mall/call_small.png" alt="">
                        <div>联系我们</div>
                    </div>
                </div>
            </div>
            <div class="orderMiddle">
                <!--多店铺订单-->
                <div class="moreShop" @touchstart="handleTouchStart"
                     @touchend="handleTouchEnd" v-show="orderItem.isMultiShop==1"
                     @click="shopOrderDetailMulti(orderItem)">
                    <img :src="skuItem.spuPicUrl" alt="" v-for="(skuItem,skuIndex) in orderItem.shop.skuList"
                         :key="'skuItem'+skuIndex">
                </div>
                <!--单店铺订单-->
                <div class="oneShop" v-show="orderItem.isMultiShop==0||orderItem.billCate2==2"
                     v-for="(skuItem,skuIndex) in orderItem.shop.skuList"
                     :key="'skuItem'+skuIndex" @click="shopOrderDetail(orderItem)">
                    <div class="goodsInfoBox">
                        <div class="goodsImg" @click.stop="goToGoodsDetails(skuItem,skuItem.activitySpuId)">
                            <img :src="skuItem.spuPicUrl" alt="">
                        </div>
                        <div class="goodsInfo">
                            <div class="goodsName">{{skuItem.spuName}}</div>
                            <div class="goodsDesc">
                                <div class="goodsClassify">{{skuItem.attrValueContent}}</div>
                                <div class="goodsNum">x{{skuItem.skuCount}}</div>
                            </div>
                        </div>
                    </div>
                    <div class="goodsFlag" v-if="orderItem.billCate2==1">等待付款</div>
                    <div class="goodsFlag" v-if="orderItem.billCate2==2">等待评价</div>
                    <div class="goodsFlag" v-else-if="orderItem.billCate2==3&&skuItem.shipStatus!='已退款'">配送中</div>
                    <div class="goodsFlag" v-else-if="orderItem.billCate2==4&&skuItem.shipStatus!='已退款'">交易完成</div>
                    <div class="goodsFlag"
                         v-else-if="(orderItem.billCate2==3||orderItem.billCate2==4)&&skuItem.shipStatus=='已退款'">已退款</div>
                    <div class="goodsFlag" v-else-if="orderItem.billCate2==6">已取消</div>
                    <div class="goodsFlag" v-else-if="orderItem.billCate2==7">{{skuItem.shipStatus}}</div>
                    <div class="goodsFlag" v-else-if="orderItem.billCate2==8">退款中</div>
                    <div class="goodsFlag" v-else-if="orderItem.billCate2==9">已退款</div>
                </div>

            </div>
            <div class="orderFoot">
                <div class="orderFootTxt" v-show="orderItem.billCate2!=2">
                    <div class="goodsNum">共{{orderItem.goodCount}}件</div>
                    <div class="goodsPrice">
                        <div class="goodsPriceTxt">应付金额：</div>
                        <div class="goodsPriceIcon">￥</div>
                        <div class="goodsPriceNum">{{orderItem.realPayTotal}}</div>
                    </div>
                </div>
                <div class="orderFootBtnList">
                    <div class="borderBtn redBorderBtn" v-if="orderItem.billCate2==1" @click="childPayFun(orderItem)">
                        去支付
                    </div>
                    <div class="orderFootBtnListBox" v-else-if="orderItem.billCate2==3">
                        <div class="borderBtn grayBorderBtn" v-show="orderItem.shop.expressCode" @click="gotoMapforExpress(orderItem)">查看物流</div>
                        <div class="borderBtn redBorderBtn" @click="Repurchase(orderItem)">再次购买</div>
                        <div class="borderBtn redBackgroundBtn" @click="confirmOrder(orderItem.shop.subBillNo)">确认收货
                        </div>
                    </div>
                    <div class="orderFootBtnListBox" v-else-if="orderItem.billCate2==2">
                        <div class="evalBtnFloat"></div>
                        <div class="borderBtn redBorderBtn" @click="Repurchase(orderItem)">再次购买</div>
                        <div class="borderBtn redBackgroundBtn" @click="toEvaluateOrder(orderItem)">评价</div>
                    </div>
                    <div class="orderFootBtnListBox" v-else-if="orderItem.billCate2==4||orderItem.billCate2==7||orderItem.billCate2==6||orderItem.billCate2==8">
                        <div class="borderBtn redBorderBtn" @click="Repurchase(orderItem,orderItem.billCate2)">再次购买</div>
                    </div>
                </div>
            </div>
        </div>
        <div class="bottomSpace"></div>
    </div>
</template>

<script>
    import {exchangeIdService} from "../../services/goodsService";
    import {
        confirmOrder,
        prepareToPayOrder,
        RepurchaseOrder,
    } from "../../services/orderListService";
    import {Dialog} from 'vant';

    export default {
        name: "orderItem",
        props: {
            lists: {
                type: Array,
                default: () => []
            },
            flag: {
                type: Number,
                default: 0
            }
        },
        data(){
            return{
                /*大字模式*/
                bindThemeL: this.$curTheme==0?false:true,

            }
        },
        activated(){
            this.bindThemeL= this.$curTheme==0?false:true;
        },
        watch: {},
        methods: {
            callShop(orderItem){
                this.$emit('setSelectOrderItem', orderItem);
            },
            handleTouchStart() {
                this.$emit('setSwipeable', false);
            },
            handleTouchEnd() {
                this.$emit('setSwipeable', true);
            },
            //去支付
            childPayFun(item) {
                const data = {
                    billNo: item.billNo
                }
                prepareToPayOrder(data).then(response => {
                    if (response.resultCode == '0') {
                        this.payParam = response.data;//请求回参
                        this.payParam.billNo = item.billNo;
                        //调用父视图支付
                        this.$emit('childPayFun', this.payParam)
                    } else {
                        Dialog.alert({
                            message: response.msg
                        }).then(() => {

                        });
                    }
                });
            },
            //评价订单
            toEvaluateOrder(item) {
                console.log("待评价" + item);
                this.$router.push({
                    name: 'myEvaluate', params: {
                        detail: {
                            billItem: item
                        },
                    }
                });
            },
            //确认收货
            confirmOrder(subBillNo) {
                Dialog.confirm({
                    title: "您确认收到该订单商品了？",
                    confirmButtonText: "确认收货",
                    cancelButtonText: "取消"
                }).then(() => {
                    const data = {
                        subBillNo: subBillNo,
                        signerType: '01',
                        signerCode: this.$userInfo.OWNER_CODE,
                    };
                    confirmOrder(data).then(response => {
                        if (response.data == true) {
                            this.$toast("收货成功");
                            this.$emit('refresh');
                        } else {
                            this.$toast("确认收货失败，请重新尝试");
                        }
                    });
                }).catch(() => {
                    // on cancel
                    Dialog.close;
                });

            },
            //再次购买
            Repurchase(order ,billCate2 = 0) {
                const data = [];
                order.shop.skuList.map((item, index) => {
                    data.push({
                        ownerCode: this.$userInfo.OWNER_CODE,
                        spuCode: item.spuCode,
                        skuNo: item.skuNo,
                        shopCode: billCate2!=6?order.shop.shopCode:item.shopCode,
                        skuCount: item.skuCount,
                        mark: 1
                    })
                });
                RepurchaseOrder(data).then(res => {
                    if (res.resultCode == 0) {
                        this.$toast(res.msg);
                        this.$router.push({name: 'shoppingCart', params: {data: data}});
                    } else {           //补货中已下架商品不允许添加购物车并提示用户
                        Dialog.alert({
                            message: res.msg
                        }).then(() => {

                        });
                    }
                });
            },
            //跳转单店铺订单详情
            shopOrderDetail(item) {
                if (item.billCate2 != 2) {
                    const _this = this;
                    const data = {
                        ownerCode: _this.$userInfo.OWNER_CODE,
                        billNo: item.billNo,
                        subBillNo: item.shop.subBillNo,
                        flag: item.billCate2,
                        item: item,
                        hasExpressStatus: item.hasExpressStatus,
                        expressCompany: item.expressCompany,
                    }

                    this.$router.push({name: 'orderDetails', params: {object: data}});
                } else {
                    const data = {
                        ownerCode: this.$userInfo.OWNER_CODE,
                        billNo: item.billNo,
                        subBillNo: item.billNo,
                        flag: 5,
                        item: item
                    }
                    this.$router.push({name: 'orderDetails', params: {object: data}})
                }

            },
            //跳转多店铺订单详情
            shopOrderDetailMulti(item) {
                const multiData = {
                    ownerCode: this.$userInfo.OWNER_CODE,
                    billNo: item.billNo,
                    flag: item.billCate2,
                    item: item
                }
                this.$router.push({name: 'orderDetails', params: {object: multiData}});
            },
            //跳转店铺首页
            goToShopPage(shopCode) {
                this.$router.push({name: 'shopPage', params: {shopCode: shopCode}});
            },
            //查看物流信息
            gotoMapforExpress(item){
                if(item.shop.expressCode=='shunfeng'||item.shop.expressCode=='dada'){
                    this.$router.push({ name: "mapForExpress", params: { subBillNo: item.billNo,expressCompany:item.expressCompany } });
                }else{
                    this.$emit('setExpressInfo',item.shop);
                }
            },
            //去商品页
            goToGoodsDetails(skuItem, activitySpuId) {
                if (!activitySpuId) {
                    this.$router.push({
                        name: 'goodsDetail',
                        params: {
                            spuCode: skuItem.spuCode,
                            goodsUrl: skuItem.spuPicUrl
                        }
                    })
                } else {
                    let paramData = {
                        activitySpuId: activitySpuId
                    }
                    exchangeIdService(paramData).then(res => {
                        if (res.resultCode == 0) {
                            if (res.data.activitySpuId) {
                                this.$router.push({
                                    name: 'flashSaleGoods',
                                    query: {
                                        spuCode: skuItem.spuCode,
                                        activitySpuId: res.data.activitySpuId,
                                        status: res.data.activityStatus,
                                        goodsUrl: skuItem.spuPicUrl
                                    }
                                })
                            } else {
                                this.$router.push({
                                    name: 'goodsDetail',
                                    params: {
                                        spuCode: skuItem.spuCode
                                    }
                                })
                            }
                        }
                    }).catch(error => {
                    });
                }

            },

        }

    }
</script>

