                        background: url(../../assets/images/mall/evaluate_tips.png) no-repeat;
                        background-size: 100% 100%;
                        border-radius: 4px;
                    }
                }

                .borderBtn {
                    width: 92px;
                    height: 30px;
                    background: #FFFFFF;
                    border-radius: 200px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-family: PingFangSC-Medium;
                    font-size: 14px;
                    letter-spacing: 0;
                    margin-right: 10px;
                }

                .borderBtn:last-child {
